<template>
	<view>
		<view v-for="item in dataList" :key="item.couponId" class="goods-cont">
			<view class="goods-item">
				<!-- <u-image :src="item.surfaceImg" width="150" height="150" border-radius="10" :fade="true" duration="450" :lazy-load="true" mode="widthFix"></u-image> -->
				<u-icon name="coupon" color="#3998EA" custom-prefix="custom-icon" size="150"></u-icon>
				<view class="goods-info">
					<text>
						标题：
						<text>{{ item.couponTitle }}</text>
					</text>
					<text>
						优惠券类型：
						<text>{{ item.couponTypeString }}</text>
					</text>
					<text>
						商品描述：
						<text>{{ item.couponDesc }}</text>
					</text>
					<text>
						使用规则：
						<text>{{ item.couponRuleData.crProduct }}</text>
					</text>
					<text>
						商品状态：
						<text>{{ item.couponRuleData.isDisable ? '正常' : '已过期' }}</text>
					</text>
					<text v-if="remaining">
						剩余数量：
						<text>{{ item.remaining }}</text>
					</text>
					<text v-if="completed">
						完成推广数量：
						<text>{{ item.completed }}</text>
					</text>
				</view>
			</view>
			<view class="goods-btns">
				<u-button class="u-m-r-20" plain ripple shape="circle" size="mini" type="primary">查看详情</u-button>
				<u-button plain ripple shape="circle" size="mini" type="error" @click="del(item.couponId)">下架</u-button>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {};
	},
	props: {
		dataList: Array,
		remaining: false,
		completed: false
	},
	methods:{
		del(id){
			this.$emit('on-del',id) 
		}
	}
};
</script>

<style lang="scss" scoped>
.goods-cont {
	width: 92%;
	margin: 20rpx 4%;
	padding: 30rpx;
	font-size: 26rpx;
	color: $u-content-color;
	background-color: #fff;
	border-radius: 20rpx;
	box-shadow: 0 2rpx 8rpx rgba(241, 241, 241, 1);
	.goods-item {
		display: flex;
		flex-direction: row;
		margin-bottom: 4rpx;
		color: $u-tips-color;

		.goods-info {
			display: flex;
			flex-direction: column;
			margin-left: 20rpx;
			& > text text {
				color: $u-content-color;
			}
		}
	}
	.goods-btns {
		padding-top: 10rpx;
		text-align: right;
	}
}
</style>
